import { createStore } from "vuex";
import InstaData from "./assets/InstaData.js";
import axios from "axios";

const store = createStore({
  state() {
    return {
      name: "kim",
      age: 20,
      likes: [12, 13, 31],
      likesCounter: [false, false, false],
      InstaData: InstaData,
      more: {},
    };
  },
  mutations: {
    setMore(state, data) {
      state.more = { data };
    },
    likes(state, i) {
      if (state.likesCounter) {
        state.likes[i]++;
        state.likesCounter = false;
      } else {
        state.likes[i]--;
        state.likesCounter = true;
      }
    },
    이름변경(state) {
      state.name = "park";
    },
    age(state, data) {
      state += data;
    },
  },
  actions: {
    getData(context) {
      axios.get("https://codingapple1.github.io/vue/more0.json").then((a) => {
        console.log(a.data);
        context.commit("setMore", a.data);
      });
    },
  },
});

export default store;
