<template>
  <div
    :class="filter + ' filter-item '"
    :style="`background-image:url(${imageData})`"
    @click="filterBtn"
  >
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: "FilterBox",
  props: {
    imageData: String,
    filter: Array,
  },
  methods: {
    filterBtn() {
      this.emitter.emit("ClickedFilterBox", this.filter);
    },
  },
};
</script>
<style>
.filter-item {
  width: 100px;
  height: 100px;
  margin: 10px 10px 10px auto;
  padding: 8px;
  display: inline-block;
  color: white;
  background-size: cover;
  background-position: center;
}
</style>
